// src/PrivacyPolicy.js
import React from 'react';
import { useTranslation } from 'react-i18next';

const PrivacyPolicy = () => {
  const { t } = useTranslation();

  return (
    <div
      className="privacy-policy-container"
      style={{ maxWidth: '800px', margin: '0 auto', padding: '20px' }}
    >
      <h1>{t('privacyPolicyTitle', 'Zsady ochrany osobnch dajov')}</h1>
      <p>
        {t(
          'privacyPolicyIntro',
          `Tieto zsady ochrany osobnch dajov (alej len "zsady") popisuj, ako zhromaujeme, pouvame a chrnime vae osobn daje. Pouitm tejto webovej strnky shlaste so spracovanm vaich dajov poda tchto zsad.`
        )}
      </p>

      <h2>{t('privacyPolicyCollectionTitle', '1. Zhromaovanie dajov')}</h2>
      <p>
        {t(
          'privacyPolicyCollectionContent',
          `Zhromaujeme osobn daje, ktor nm poskytnete prostrednctvom registrcie, objednvok alebo kontaktnch formulrov. Medzi tieto daje mu patri vae meno, e-mailov adresa, telefnne slo a alie informcie, ktor s potrebn na poskytovanie naich sluieb.`
        )}
      </p>

      <h2>{t('privacyPolicyUsageTitle', '2. Pouvanie dajov')}</h2>
      <p>
        {t(
          'privacyPolicyUsageContent',
          `Vae daje pouvame na spracovanie objednvok, komunikciu s vami, poskytovanie zkaznckej podpory a zlepovanie naich sluieb. Vae informcie meme tie vyui na marketingov ely, pokia ste nm dali na to shlas.`
        )}
      </p>

      <h2>{t('privacyPolicyProtectionTitle', '3. Ochrana dajov')}</h2>
      <p>
        {t(
          'privacyPolicyProtectionContent',
          `Zavdzame prsne bezpenostn opatrenia, aby sme zabezpeili, e vae osobn daje s chrnen pred neoprvnenm prstupom, zverejnenm alebo zneuitm.`
        )}
      </p>

      <h2>{t('privacyPolicyRightsTitle', '4. Vae prva')}</h2>
      <p>
        {t(
          'privacyPolicyRightsContent',
          `Mte prvo na prstup k vaim osobnm dajom, ich opravu alebo vymazanie. Ak mte akkovek otzky ohadom spracovania vaich dajov, nevhajte ns kontaktova.`
        )}
      </p>

      <h2>{t('privacyPolicyContactTitle', '5. Kontakt')}</h2>
      <p>
        {t(
          'privacyPolicyContactContent',
          `V prpade akchkovek otzok alebo pripomienok ohadom naich zsad ochrany osobnch dajov ns kontaktujte na:`
        )}{' '}
        <a href="mailto:dsnextgen.eu@gmail.com">dsnextgen.eu@gmail.com</a>
      </p>

      <p>
        <em>{t('privacyPolicyUpdated', 'Posledn aktualizcia: 10.02.2025')}</em>
      </p>
    </div>
  );
};

export default PrivacyPolicy;
