// src/CookiePolicy.js
import React from 'react';
import { useTranslation } from 'react-i18next';

const CookiePolicy = () => {
  const { t } = useTranslation();

  return (
    <div className="cookie-policy-container">
      <h1>{t('cookiePolicyTitle', 'Cookie Policy')}</h1>
      <p>
        {t(
          'cookiePolicyContent',
          `Tto strnka pouva cookies, aby vm poskytla lep zitok. 
          Cookies s mal textov sbory uloen vo vaom prehliadai. 
          Pouvame ich na zabezpeenie zkladnej funknosti strnky, 
          analytiku a marketingov ely. Pre viac informci si pretajte nau 
          zsadu ochrany osobnch dajov.`
        )}
      </p>

      <hr />

      <p>
        <strong>Posledn aktualizcia:</strong> 10.02.2025
      </p>

      <h2>1. o s cookies?</h2>
      <p>
        Cookies s mal textov sbory, ktor sa uloia vo vaom prehliadai, ke navtvite webov strnku. Pomhaj webovm strnkam zapamta si informcie o vaej nvteve, o zlepuje vau sksenos (naprklad tm, e si pamtaj vae preferencie alebo prihlsenie).
      </p>

      <h2>2. Ako pouvame cookies</h2>
      <p>
        Na naej strnke <strong>fuely.sk</strong> pouvame cookies z nasledujcich dvodov:
      </p>
      <ul>
        <li>
          <strong>Zkladn funknos:</strong> Umouj nm zabezpei, e strnka funguje sprvne. Patria sem cookies, ktor pamtaj, i ste prihlsen, a udriavaj vae nastavenia.
        </li>
        <li>
          <strong>Analytika a tatistiky:</strong> Pomocou cookies sledujeme, ako nvtevnci pouvaj nau strnku, o nm pomha vylepi obsah a funkcionalitu. Tieto daje s anonymn.
        </li>
        <li>
          <strong>Marketing a personalizcia:</strong> V niektorch prpadoch meme pouva cookies na zlepenie reklm a zobrazenie relevantnho obsahu. Tieto cookies mu by nastaven aj tretmi stranami.
        </li>
      </ul>

      <h2>3. Typy pouvanch cookies</h2>
      <ul>
        <li>
          <strong>Relian (Session) cookies:</strong> Doasn cookies, ktor sa vyma po zatvoren prehliadaa.
        </li>
        <li>
          <strong>Trval cookies:</strong> Cookies, ktor zostan uloen vo vaom zariaden aj po zatvoren prehliadaa a pomhaj nm zapamta si vae preferencie pri alch nvtevch.
        </li>
        <li>
          <strong>Prv strany a tretie strany:</strong> Niektor cookies nastavujeme my priamo (prv strany), zatia o in mu by nastaven naimi partnermi (tretie strany).
        </li>
      </ul>

      <h2>4. Tretie strany</h2>
      <p>
        Niektor sluby, ktor pouvame (naprklad analytick nstroje a socilne siete), mu tie pouva vlastn cookies. Tieto cookies s spravovan prslunmi spolonosami. Pre viac informci o ich zsadch ochrany osobnch dajov odporame navtvi ich webov strnky.
      </p>

      <h2>5. Ako spravova a odstrni cookies</h2>
      <p>
        Vina modernch prehliadaov umouje nastavi, ako chcete s cookies zaobchdza  mete ich blokova, odstrni alebo upozorni, ke sa cookie uklad. Viac informci njdete v nastaveniach vho prehliadaa. Majte vak na pamti, e zablokovanie cookies me ovplyvni funknos niektorch ast naej strnky.
      </p>

      <h2>6. Zmeny v Cookie Policy</h2>
      <p>
        Tto Cookie Policy me by asom aktualizovan. Odporame vm pravidelne kontrolova tto strnku, aby ste boli informovan o prpadnch zmench.
      </p>

      <h2>7. Kontakt</h2>
      <p>
        Ak mte akkovek otzky alebo pripomienky k tejto Cookie Policy, nevhajte ns kontaktova na adrese: <a href="mailto:dsnextgen.eu@gmail.com">dsnextgen.eu@gmail.com</a>
      </p>
    </div>
  );
};

export default CookiePolicy;
