// src/TermsOfUse.js
import React from 'react';
import { useTranslation } from 'react-i18next';

const TermsOfUse = () => {
  const { t } = useTranslation();

  return (
    <div className="terms-of-use-container" style={{ maxWidth: '800px', margin: '0 auto', padding: '20px' }}>
      <h1>{t('termsOfUseTitle', 'Podmienky pouvania')}</h1>

      <p>
        {t(
          'termsOfUseIntroduction',
          `Tieto podmienky pouvania (alej len "podmienky") upravuj prstup a pouvanie tejto webovej strnky. Pouitm tejto strnky shlaste s dodriavanm tchto podmienok.`
        )}
      </p>

      <h2>1. Prstup k strnke</h2>
      <p>
        {t(
          'termsOfUseAccess',
          `Pouvate m prvo prstupu k informcim a obsahu tejto strnky, avak tento prstup je podmienen dodriavanm tchto podmienok.`
        )}
      </p>

      <h2>2. Zodpovednos</h2>
      <p>
        {t(
          'termsOfUseLiability',
          `Prevdzkovate webovej strnky nenesie zodpovednos za kody, ktor mu vznikn v dsledku pouitia alebo nemonosti pouitia informci a sluieb poskytovanch touto strnkou.`
        )}
      </p>

      <h2>3. Ochrana osobnch dajov</h2>
      <p>
        {t(
          'termsOfUsePrivacy',
          `Informcie o spracvan a ochrane osobnch dajov njdete v naej Zsade ochrany osobnch dajov, ktor je neoddelitenou sasou tchto podmienok.`
        )}
      </p>

      <h2>4. Autorsk prva</h2>
      <p>
        {t(
          'termsOfUseCopyright',
          `Vetok obsah, dizajn a texty na tejto strnke s chrnen autorskmi prvami a inmi prvnymi predpismi. Akkovek neoprvnen koprovanie, renie alebo in pouitie je prsne zakzan.`
        )}
      </p>

      <h2>5. Zmeny podmienok</h2>
      <p>
        {t(
          'termsOfUseChanges',
          `Prevdzkovate si vyhradzuje prvo kedykovek meni tieto podmienky. Aktulna verzia podmienok bude vdy zverejnen na tejto strnke.`
        )}
      </p>

      <h2>6. Kontakt</h2>
      <p>
        {t(
          'termsOfUseContact',
          `Ak mte akkovek otzky alebo pripomienky k tmto podmienkam pouvania, nevhajte ns kontaktova na:`
        )}{' '}
        <a href="mailto:dsnextgen.eu@gmail.com">dsnextgen.eu@gmail.com</a>
      </p>

      <p>
        <em>{t('termsOfUseUpdated', 'Posledn aktualizcia: 10.02.2025')}</em>
      </p>
    </div>
  );
};

export default TermsOfUse;
